.page-footer {
  background-color: $color-gray-50;

  &__container {
    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 164px 1fr 164px;
      gap: 16px;
      padding: 46px 0 45px 0;
    }

    @media (min-width: $desktop-width) {
      padding: 44px 0 43px 0;
    }
  }
}
